import React, { Component } from 'react';
import '../App.css';
import '../App.js';
import './whatis.css';
import laptop from './img/screen.jpg';



class WhatIs3D extends Component {
    render() {
        return (
            <div className="row">
                <div className="col-lg-6 col-md-6 col-sm-12  mt-5 mb-5">
                    <img src={laptop} className="whoweare-img" alt="laptop"/>
                </div>    
                <div className="col-lg-6 col-md-6 col-sm-12 mt-5 mb-5">
                    <h1 className=" mt-5 mb-5">Who we are?</h1>
                    <p>Since its inception, Impossible Triangle has been defining the 3D printing industry.
                Founded in <b>2011</b>, Impossible Triangle is led by folks who've spent most of their careers in startups,
                         and combine serious technical chops with an inspiring vision of what the world could be.
                 Were bringing together a passionate, dynamic team of game changers.</p>
                </div>
            </div>
        )
    }
}
export default WhatIs3D


