import React, { Component } from 'react';
// import logo from './img/plastic_red.png';
import '../App.css';
import '../App.js';
import './materials.css';
import material1 from './img/copper.png';
import material2 from './img/gold.png';
import material3 from './img/hda.png';
import material4 from './img/frosted_plastic.png';
import material5 from './img/silver.png';

import img1 from '../img/index/1.png';
import img2 from '../img/index/2.png';
import img3 from '../img/index/3.png';

var materials = [
    {
        material: img1,
        index: 1,
        alt: 'printing materials pic'
    },
    {
        material: img2,
        index: 2,
        alt: 'printing materials pic'
    },
    {
        material: img3,
        index: 3,
        alt: 'printing materials pic'
    }
];
var materialImg = materials.map((each) => (

    <img src={each.material} alt={each.alt} key={each.material} className="col-sm-6 col-md-4 col-lg-4 pr-1 pl-1 mt-2 mb-2" />
));



class Materials extends Component {
    render() {
        return <React.Fragment>
            <h1 className="mt-5 mb-5">STATE OF THE ART MACHINES</h1>
            <div className="row mt-3 mb-5">
                {materialImg}
            </div>
        </React.Fragment>
    }
}

export default Materials;


