import React, { Component } from 'react';
// import logo from './img/plastic_red.png';
import '../App.css';
import '../App.js';
import logo from '../img/logo_g.png';
import './menu.css';
import {
    Route,
    NavLink,
    HashRouter
} from "react-router-dom";
import Login from '../Login/Login.js';
import Gallery from '../Gallery/Gallery';
import Materials from '../Materials/Materials.js';
import Carousel from '../Carousel/Carousel.js';
import Contacts from '../Contacts/Contacts.js';
import Upload from '../Upload/Upload.js';
// import toggleClass from './menu-jq.js';


const links = [
    {
        href: "/",
        text: 'Home',
        class: 'nav-link active'
    },
    {
        href: "/gallery",
        text: 'Gallery',
        class: 'nav-link'
    },
    {
        href: "/contacts",
        text: 'About us',
        class: 'nav-link'
    },
    {
        href: "/upload",
        text: "Upload",
        class: 'nav-link menuBtn'
    },
    // {
    //     href: "/aboutus",
    //     text: 'About us',
    //     class: 'nav-link'
    // },
]


const menuList = links.map((eachLink) => (
    <li className='nav-item' key={eachLink.href.toString()}>
        <NavLink to={eachLink.href} className={eachLink.class}>{eachLink.text}</NavLink>
    </li>
));

// function toggleClass() {
//     var element = document.getElementById("hamburger-menu");
//     element.classList.add("is-active");
//  }



class Menu extends Component {
    render() {
        return (
            <React.Fragment>
                <div id="hamburger-menu">
                    <span className="icon-bar line1"></span>
                    <span className="icon-bar line2"></span>
                    <span className="icon-bar line3"></span>
                </div>
    
                <div className="justify-content-between row align-items-center align-middle mt-3 mb-3">
                <div className="col-sm-6 col-md-2 col-lg-2">
                    <a className="navbar-brand" href="index.html">
                        <img src={logo} className="img-logo" alt="company's logo" />
                    </a>
                </div >
                <div className="col-sm-10 col-md-10 col-lg-10 menu-navigation">
                    <ul className="nav justify-content-end">
                        {menuList}
                        <li className='nav-item'>
                            <Login />
                        </li>
                    </ul>
                </div>
                </div>
                </React.Fragment>    
        )
    }
}



export default Menu;


