import React, { Component } from 'react';
import '../App.css';
import '../App.js';
import './upload.css';
import ReactFragment from 'react';
import LoginInput from '../Login/loginInput.js';
import Login from '../Login/Login.js';
import visa from './payments/visa.png';
import paypal from './payments/paypal.png';
import mastercard from './payments/mastercard.png';
var contacts = [
    {
        label: 'First Name',
        inputType: 'text',
        placeholder: 'Enter your first name',
        index: 1,
    },
    {
        label: 'Last Name',
        inputType: 'text',
        placeholder: 'Enter your last name',
        index: 1,
    },
    {
        label: 'Credit Card Number',
        inputType: 'text',
        placeholder: '1234 ____ ____',
        index: 3,
    }
]

let expDateEach;

let contactForm = contacts.map((each) => (
    <div className="col-6" key={each.label}>
        <label htmlFor={each.label}>{each.label}</label>
        <input type={each.inputType} id={each.label} className="form-control" placeholder={each.placeholder} />
    </div>
));

let expDateArray = ['January', 'February', 'March', 'April', 'May', 'June',
    'July', 'August', 'September', 'October', 'November', 'December', 'Months'];

let d = new Date();
let n = d.getFullYear();

let years = [];
for (let i = n; i <= 2028; i++) {
    years.push(i);
}

let expYear = <select>
    {years.map(year => <option key={year}>{year}</option>)}
</select>;

let expDate = expDateArray.map(date =>
    <option key={date}>{date}</option>
)
let options = <select>
    {expDate}
</select>;



class CardDetails extends Component {
    render() {
        return (
            <fieldset>
                <h1>Card details</h1>

                {/* payment options */}


                <div className="row justify-content-md-center mt-5 mb-5">
                    <div className="col-2">Payment options
                    </div>
                    <div className="col-2">
                        <label className="form-check-label">
                            <input type="radio" className="form-check-input" name="optionsRadios" value="option1" />
                            <img src={visa} alt="visa" className="img-fluid" />
                        <img src={mastercard} alt="mastercard" className="img-fluid" />
                        </label>
                    </div>
                    <div className="col-2">
                        <label className="form-check-label">
                            <input type="radio" className="form-check-input" name="optionsRadios" value="option2" />
                            <img src={paypal} alt="paypal" className="img-fluid" />
                        </label>
                    </div>
                </div>

                {/* form-block */}


                <form className="form-block">
                    <div className="form-group row" >{contactForm}</div>
                    <span className="col-4">Expiration Date</span>
                    <span className="col-4">{options}</span>
                    <span className="col-4">    {expYear}</span>
                </form>
            </fieldset>
        );
    }
}

export default CardDetails;



