import React, { Component } from 'react';
// import logo from './img/plastic_red.png';
import '../App.css';
import '../App.js';
import './login.css';

var contacts = [
    {
        label: 'Name',
        inputType: 'name',
        placeholder: 'Login',
        index: 1,
        for: 'email',
    },
    {
        label: 'Password',
        inputType: 'password',
        placeholder: 'Password',
        index: 2,
        for:'psw',
    }
]
var contactForm = contacts.map((each) => (
    <div className="form-group" key={each.index}>
        <label htmlFor={each.for}>{each.label}</label>
        <input type={each.inputType} className="form-control" placeholder={each.placeholder} name={each.for} required/>
    </div>
));


class LoginInput extends Component {
    render() {
        return (
            <form className="form-block">
                {contactForm}
            </form>
        );
    }
}

export default LoginInput;



